import React from 'react'
import '../mystyles.css'
import Home from './Home'
import About from './About'
import Skills from './Skills'
import Projects from './Projects'
import Contact from './Contact'
import Proficiencies from './Proficiencies'

class Nav extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      nav_list: [{
        id: "first",
        data_ref: "home"
      },
      {
        id: "second",
        data_ref: "about"
      },
      {
        id: "third",
        data_ref: "skills"
      },
      {
        id: "fourth",
        data_ref: "projects"
      },
      {
        id: "five",
        data_ref: "contact"
      }]
    }
  }
  render() {
    return (
      <div className="container-fluid" ref={el => (this.div = el)}>
        <div className="behindediv">
          <nav id="navbar" className="group">
            <ul>
              {this.state.nav_list.map((ele) =>
                <li id={ele.id} data-ref={ele.data_ref}><a href={`#${ele.data_ref}`}>{ele.data_ref}</a></li>
              )}

            </ul>
          </nav>
        </div>
        <div className="screen" id="home">
          <Home />
        </div>
        <div className="screen" id="about">
          <About />
        </div>

        <div className="screen" id="skills">
          <Skills />
        </div>
        <div className="screen" id="projects">
          <Projects />
        </div>
        <div className="screen" id="Proficiences">
          <Proficiencies />
        </div>
        <div className="screen" id="contact">
          <Contact />
        </div>
      </div>
    )
  }
}
export default Nav